:root{
  --green-color:#3eb969;
  --blue-color:#38b6ff;
  --orange-color:#ff914d;
  --purple-color:#ff66c4;
  --red-color:#ff1616;
  --yellow-color:#ffde59;
  --black-color:rgba(0,0,0);
}
@font-face{
  font-family: mokoto;
  src: url(./fonts/Mokoto/MokotoGlitch.ttf);
}
body{
    background-color: var(--black-color);
}
.bg{
    background-color: var(--black-color);
}
.progressItem{
    font-family: 'mokoto';
    background: transparent;
}
.para, .brandG{
    color: white !important;
    font-family: 'mokoto';
}
.roadMapText{
    font-family: 'mokoto';
    line-height: 38px;
}
.navbar {
 padding: 1rem 2rem;
 height: 150px;
}
.navbar-brand{
    font-family: 'mokoto';
    font-size: 70px;
}
.navbar-collapse{
    border-left: 2px solid white;
    padding: 1rem 1rem;
}
.navbar-toggler {
    background-color: white !important; 
}
.collapse.show {
    display: block;
    z-index: 1;
    background-color: black;
}
.navItem > a{
   font-family: 'mokoto';
   font-size: 25px;
   color: white;
}
.navItem > a:hover{
    color: #3eb963 !important;
}
a.MINT.active{color: var(--green-color) !important;}
a.ABOUT.active{color: var(--blue-color) !important;}
a.ROADMAP.active{color: var(--orange-color) !important;}
a.TEAM.active{color: var(--red-color) !important;}
a.FAQS.active{color: var(--purple-color) !important;}
.shapeContainer{
    min-height: 78vh;
    display: flex;
    align-items: center;
    justify-content: start;
}
.shape{
    clip-path: polygon(0 0, 70% 15%, 70% 85%, 0% 100%);
    height: 90%;
    
}
.mainTitle{
    font-family: 'mokoto';
    font-size: 130px;
    margin-top: -60px;
    margin-bottom: 0;
}
.mainTitle > .span1{ color:var(--green-color);}
.mainTitle > .span2{ color:var(--blue-color);}
.mainTitle > .span3{ color:var(--orange-color);}
.mainTitle > .span4{ color:var(--purple-color);}
.mainTitle > .span5{ color:var(--red-color);}
.mainTitle > .span6{ color:var(--yellow-color);}
.minted{ font-size: 30px;}
.mintBtn{ 
    display: flex;
    align-items: center;
    cursor: pointer;
    height: 70px;
    width: 300px;
    font-family:'mokoto';
    font-size: 20px;
    justify-content: center;
    /* padding: 0px 15px; */
    background-color:var(--green-color);
    border-color:var(--green-color);
    color: var(--black-color);
}
// about page 
.joinBtn{ 
        font-family:'mokoto';
        font-size: 40px;
        padding: 5px 15px;
        background:var(--green-color);
        border-radius: 20px;
        border:5px solid #505050 !important;
        color: var(--black-color);
}
.yrm{
    font-family:'mokoto';
    font-size: 30px;
    padding: 5px 15px;
    background:var(--purple-color);
    border-radius: 20px;
    border:5px solid white !important;
    color: white;
}
.aboutContainer{
    display: flex;
    flex-direction: column;
    height: 78vh;
}
.aboutHeading{
    font-size: 40px;
    font-family: 'mokoto';
}
.aboutText{
    text-align: center;
}
.aboutText1{
    text-align: center;
    font-size: 1.3rem;
}
// roadmap page 
.endGame{
    background-image: url('./images/11.png');
    background-size: cover;
    background-position: center;
    height: 100vh;
}
@media(max-width:992px){
    .navItem{
        height: auto;
    }
}
@media(max-width:810px){
    .shapeContainer,.arrow{
        display: none;
    }
    .mainTitle{
        font-size: 90px;
        margin-top: 0;
    }
    .navbar {
        height: 100px;
    }
    .mintContainer{
        display: flex;
        justify-content: center;
    }
    .aboutContainer{
        height: auto;
        margin-top: 20px;
    }
}
@media(max-width:530px){
    .joinBtn{
        font-size: 1rem;
    }
    .mainTitle{
        font-size: 63px;
    }
    .navbar {
        height: 110px;
    }
}
.font-m{
    font-size: 14px;
    @media  screen and (max-width:500px) {
        font-size: 11px;
    }
}
.mt-4{
    margin-top: 2rem;
}

.MintNum-top{
    display: flex;
    justify-content: space-between;
    width: 300px;
    height: 60px;
}
.mint-plus-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 60px;
    width: 60px;
    font-family:'mokoto';
    font-size: 40px;
    background-color:var(--green-color);
    border-color:var(--green-color);
    color: var(--black-color);
}
.mintNum-input{
    display: flex;
    text-align: center;
    width: 50%;
    background-color: transparent;
    border-radius: 7px;
    border: 3px solid;
    border-color:var(--green-color);
    outline: none;
    font-size: 20px;
    
}
.team-img-medium{
    height: 192px;
}
.team-img-small{
    height: 143px;
}
.font-s{
    font-size:0.9rem
}
.social-holder{
    display: flex;
    
    img{
        height: 45px;
        cursor: pointer;
        &:hover{
            height: 50px;
        }
    }
    margin-left: auto;
    margin:auto 0px auto auto;
    @media screen and (max-width:992px) {
        margin:auto auto auto 0px;
    }
}